.button {
  padding: 16px 32px;
  border-radius: 2px;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  border: 1px solid transparent;
  box-sizing: border-box;
  display: flex;
  gap: 10px;
}
.button:hover {
  cursor: pointer;
}

.primary {
  background-color: #2231aa;
  color: #ffffff;
}
.primary:hover {
  background-color: #5360cd;
}

.secondary {
  background-color: #e8e8e8;
  color: #313037;
}
.secondary:hover {
  background-color: #dadada;
}

.error {
  color: #fd3419;
}

.error:hover {
  border: 1px solid #dadada;
}

.disabled {
  background-color: #e8e8e8;
  color: #8d8e97;
  pointer-events: none;
}

.error.disabled {
  background-color: transparent;
}
.like,
.dislike,
.bookmark {
  background-color: #e8e8e8;
}

.like:hover,
.bookmark:hover {
  background-color: #5360cd;
  color: #ffffff;
}

.bookmark:hover * {
  fill: #ffffff;
}
.like:hover * {
  fill: #ffffff;
}

.dislike:hover {
  background-color: #fd3419;
}
.dislike:hover * {
  fill: #ffffff;
}